/*
* @subsection   Isotope
*/

$isotope-default-color: $black;
$isotope-default-color-active: $primary;
$isotope-default-border: $isotope-default-color-active;
$isotope-nav-spacing: 35px;

$isotope-default-item-offset: 30px;

$isotope-inverse-color: $white;
$isotope-inverse-color-active: $primary;
$isotope-inverse-border: $isotope-inverse-color-active;

// Mixin for isotope elements spacing
@mixin isotope-gutter($isotope-gutter) {
	transform: translateY(-$isotope-gutter);
	margin-bottom: -$isotope-gutter;
	
	[class*="col-"] {
		margin-top: $isotope-gutter;
	}
	
	&:after {
		margin-top: $isotope-gutter / 2;
	}
}

// Isotope Layout
[data-isotope-layout] {
	position: relative;
	display: block;
	transition: .4s all ease;
	min-height: 160px;
	
	&:after {
		content: '';
		position: absolute;
		width: 64px;
		height: 64px;
		top: 50%;
		left: 50%;
		background-image: url('data:image/png;base64,');
		background-position: -1152px 0;
		animation: 0.7s sprite-animation steps(18) infinite;
		transition: .4s all ease;
		transform: translate(-50%, -50%);
	}
	
	[class*="col-"] {
		display: block;
		opacity: 0;
		transition: .4s opacity ease;
		overflow: hidden;
		will-change: opacity, transform;
	}
	
	&.isotope--loaded {
		[class*="col-"] {
			opacity: 1;
		}
		
		&:after {
			opacity: 0;
			visibility: hidden;
		}
	}
}

// Isotope gutters
.isotope-gutter-default {
	position: relative;
	@include isotope-gutter($isotope-default-item-offset);
}

// Isotope filters common styles
.isotope-filters {
	$value: 10px;
	
	> * {
		margin-top: 0;
		vertical-align: middle;
	}
	
	.list-inline {
		position: relative;
		transform: translateY(-$value);
		margin: 0;
		margin-bottom: -$value;
		
		li {
			display: inline-block;
			margin-top: $value;
		}
		
		a {
			position: relative;
			transition: .3s;
		}
	}
	
	@include media-breakpoint-up(lg) {
		.isotope-filters-trigger {
			display: none;
		}
	}
}

// Isotope filters minimal
.isotope-filters-minimal {
	.list-inline {
		> li {
			padding: 0;
		}
	}
	@include media-breakpoint-up(lg) {
		.list-inline {
			word-spacing: 0;
			
			> li:not(:last-child) {
				margin-right: $isotope-nav-spacing;
			}
			
			a {
				position: relative;
				padding-bottom: 5px;
				font: 700 14px/24px $font-family-sec;
				
				&:after {
					content: '';
					position: absolute;
					bottom: 0;
					left: 0;
					right: 100%;
					height: 2px;
					transition: .33s all ease;
					background: $isotope-default-border;
				}
				
				&,
				&:active,
				&:focus {
					color: $isotope-default-color;
				}
				
				&:hover,
				&.active {
					color: $isotope-default-color-active;
				}
				
				&.active {
					&:after {
						right: 0;
					}
				}
			}
		}
	}
}

// Mobile version of filters
.isotope-filters-responsive {
	position: relative;
	z-index: 10;
	
	*:focus {
		outline: none;
	}
	
	@include media-breakpoint-down(sm) {
		max-width: 370px;
		margin-left: auto;
		margin-right: auto;
	}
	
	@include media-breakpoint-down(md) {
		vertical-align: middle;
		
		.isotope-filters-toggle {
			padding-left: 10px;
			padding-right: 10px;
			
			.caret {
				margin-left: 5px;
			}
		}
		
		> li {
			vertical-align: middle;
			padding: 0;
		}
		
		> li:first-child {
			margin-right: 8px;
		}
		
		> li + li {
			margin-top: 8px;
		}
		
		.isotope-filters {
			position: absolute;
			top: 49px;
			left: 0;
			z-index: 10;
			width: 100%;
			min-width: 210px;
			padding: 15px;
			background: $white;
			border-radius: 3px;
			text-align: left;
			visibility: hidden;
			opacity: 0;
			transition: .33s all ease;
			border: 1px solid darken($white, 5%);
			
			&.active {
				visibility: visible;
				opacity: 1;
			}
			
			.list-inline {
				width: 100%;
			}
			
			.list-inline a {
				display: inline-block;
				width: 100%;
				padding: 3px 8px;
				font-size: 12px;
				border-radius: 2px;
				color: $black;
				border: 0;
				background-color: transparent;
				
				&.active,
				&:hover {
					color: $primary;
					background-color: rgba($primary, .1);
				}
			}
			
			li {
				display: block;
				width: 100%;
			}
			
			li + li {
				margin-top: 6px;
			}
		}
		
		@include media-breakpoint-up(sm) {
			> li {
				display: inline-block;
				margin: 0;
			}
			
			> li + li {
				margin-top: 0;
			}
		}
		
		@include media-breakpoint-up(md) {
			.isotope-filters {
				min-width: 250px;
				.list-inline a {
					padding: 5px 10px;
				}
			}
		}
	}
	
	@include media-breakpoint-up(lg) {
		> li:first-child {
			display: none;
		}
	}
}

.caret {
	display: inline-block;
	width: 0;
	height: 0;
	margin-left: 2px;
	vertical-align: middle;
	border-top: 4px dashed;
	border-top: 4px solid \9
;
	border-right: 4px solid rgba(0, 0, 0, 0);
	border-left: 4px solid rgba(0, 0, 0, 0);
}

.isotope-filters-toggle {
	display: block;
	border: 0;
	outline: 0;
	margin-left: auto;
	margin-right: auto;
	border-radius: 6px;
	
	@include media-breakpoint-up(lg) {
		display: none;
	}
}

// Style: inverse
.isotope-style-inverse {
	@include media-breakpoint-up(lg) {
		.list-inline {
			> li > a {
				&:after {
					background: $isotope-inverse-border;
				}
				
				&,
				&:active,
				&:focus {
					color: $isotope-inverse-color;
				}
				
				&:hover,
				&.active {
					color: $isotope-inverse-color-active;
				}
			}
		}
	}
}

// Cobbles items
.isotope-item-width-33p,
.isotope-item-width-66p {
	width: 100%;
	
	> * {
		position: relative;
		transform: scale(1.002);
	}
	
	@include media-breakpoint-up(md) {
		width: 50%;
	}
}

@include media-breakpoint-up(lg) {
	.isotope-item-width-33p {
		width: 33.33333%;
	}
	
	.isotope-item-width-66p {
		width: 66.66666%;
	}
}

// Large 992   1200 Extra Large
@include media-breakpoint-between(lg, lg) {
	.isotope-cust-br {
		.col-lg-4 {
			width: 33.2%;
		}
	}
}

.isotope-condensed {
	.isotope-item {
		overflow: hidden;
	}
	
	@include media-breakpoint-up(lg) {
		.isotope-item {
			margin: -1px;
		}
	}
}

[data-x-mode="design-mode"] [data-isotope-layout] {
	[class*="col-"] {
		opacity: 1;
	}
	
	&:after {
		opacity: 0;
		visibility: hidden;
	}
}
