.overlay-5 {
	position: relative;
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba($brand-ebony-clay, .5);
	}
}

.overlay-8 {
	position: relative;
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba($brand-ebony-clay, .8);
	}
}

.overlay-9 {
	position: relative;
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba($brand-ebony-clay, .9);
	}
}

[class*='overlay-'] > * {
	position: relative;
	z-index: 1;
}

.page-title-wrap.overlay-5 > * {
	position: relative;
	z-index: 1;
}
.page-title-wrap.overlay-8 > * {
	position: relative;
	z-index: 1;
}

.parallax-container {
	&.overlay-5 {
		&::after {
			z-index: 1;
		}
	}
}

.parallax-container {
	&.overlay-8 {
		&::after {
			z-index: 1;
		}
	}
}

.section-image-aside-img {
	.section-bordered-inside {
		background-color: rgba($brand-ebony-clay,.9);
	}
}

.swiper-slide.swiper-slide-bottom {
	background-position: center bottom;
}

.rd-navbar-brand {
	img {
		@include media-breakpoint-down(md) {
			max-width: 70%;
		}
	}
}

.rd-search-results-live {
	.search_error {
		color: $white;
	}
}

.swiper-variant-1 .swiper-button-prev, .swiper-variant-1 .swiper-button-next {
	&:hover {
		color: $primary;
	}
}

.owl-prev, .owl-next, .owl-dot {
	cursor: pointer;
}

.owl-dot.active {
	cursor: auto;
}

.dtp-date > div {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
}
.form-btn-wrp {
	.button {
		@include media-breakpoint-down(sm) {
			padding-left: 0;
			padding-right: 0;
		}
	}
}

.shilder-header-with-divider {
	@include media-breakpoint-down(lg) {
		display: none;
	}
	display: inline-block;
}

.swiper-container {
	.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
		opacity: 1;
	}
}

.btn {
	cursor: pointer;
}

.form-wrap-recaptcha {
	.form-validation {
		left: 10px;
		right: auto;
	}
}
