//
// Custom Buttons Styles
// --------------------------------------------------

// Base styles
// --------------------------------------------------

.button {
  max-width: 100%;
  padding: $padding-base-vertical $padding-base-horizontal;
  font-family: $font-family-serif;
  font-size: $btn-font-size * 1px;
  line-height: $line-height-base;
  font-weight: $btn-font-weight;
  border-radius: $btn-border-radius;
  border: 2px solid;
  transition: .3s ease-out;
  text-align: center;
  text-transform: capitalize;

  @include media-breakpoint-up(xl) {
    padding-left: 57px;
    padding-right: 57px;
  }

  &.big{
    
    // Medium 576px
    @include media-breakpoint-up(sm) {
      font-size: 18px;
    }
  }


  &:focus,
  &:active,
  &:active:focus {
    outline: none;
  }

  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
    }
  }

  &:hover,
  &:focus,
  &.focus {
  }

  &:active,
  &.active {
    box-shadow: none;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
  }

  &-smaller {
    padding: 8px 25px;
  }

  &-small {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.buttons-inset-horizontal-15 {
  padding-left: 15px;
  padding-right: 15px;
}

// Alternate buttons
// --------------------------------------------------
html {
  .button-default {
    @include button-variant-custom($white, $abbey, $abbey, $white, $primary, $primary);
  }

  .button-primary {
    @include button-variant-custom($btn-primary-color, $btn-primary-bg, $btn-primary-border, $white, #e3b536, #e3b536);
  }

  .button-red-orange {
    @include button-variant-custom($white, $red-orange, $red-orange, $white, $gray-dark, $gray-dark);
  }

  .button-primary-contrast {
    @include button-variant-custom($white, $primary, $primary, $white, #e3b536, #e3b536);
  }

  .button-primary-outline {
    @include button-variant-custom($primary, transparent, $primary, $white, $primary, $primary);
  }

  .button-white-outline {
    @include button-variant-custom($white, transparent, $white, $tundora, $white, $white);
  }

  .button-white-outline-variant-1 {
    @include button-variant-custom($white, transparent, $white, $white, $primary, $primary);
  }

  .button-silver-outline {
    @include button-variant-custom($black, transparent, $silver, $white, $silver, $silver);
  }

  .button-black-outline {
    @include button-variant-custom($black, transparent, $black, $white, $black, $black);
  }

  .button-ebony-clay-outline {
    @include button-variant-custom($brand-ebony-clay, transparent, $brand-ebony-clay, $white, $brand-ebony-clay, $brand-ebony-clay);
    border-width: 2px;
  }

  .button-ebony-clay {
    @include button-variant-custom($white, $brand-ebony-clay, $brand-ebony-clay, $white, $primary, $primary);
    border-width: 2px;

    .icon{
      transition: .3s !important;
    }
    &:hover{
      .icon.text-primary{
        color: #fff;
      }
    }
  }

  // Success appears as green
  .button-success {
  }

  // Info appears as blue-green
  .button-info {
  }

  // Warning appears as orange
  .button-warning {
  }

  // Danger and error appear as red
  .button-danger {
  }
}

.page {

}


// Button Sizes
// --------------------------------------------------

.button-xs {
  min-width: 100px;
  @include button-size($padding-xs-vertical, $padding-xs-horizontal, 11px, $line-height-base, $border-radius-large);
}

.button-sm {
  min-width: 120px;
  @include button-size($padding-small-vertical, $padding-small-horizontal, 16px, $line-height-base, $border-radius);
}

.button-lg {

  @include button-size($padding-large-vertical, $padding-large-horizontal, 13px, $line-height-base, $border-radius);

  @include media-breakpoint-up(md) {
    min-width: 270px;
  	font-size: 18px;
  }
}

.button-xl {
  @include button-size(18px, 40px, 16px, $line-height-base, $border-radius);

  @include media-breakpoint-up(md) {
    padding: $padding-extra-large-vertical $padding-extra-large-horizontal;
    font-size: 18px;
  }

  // Variants
  @include media-breakpoint-up(lg) {
    min-width: 270px;
    &-bigger {
      padding-top: 28px;
      padding-bottom: 28px;
    }
  }
}

// Button Shapes
// --------------------------------------------------

.button-block {
  display: block;
  min-width: 30px;
  width: 100%;
}

.button-rect {
  border-radius: 0;
}

.button-round {
  border-radius: 12px;
}

.button-circle {
  border-radius: 35px;
}

.button-round-bottom {
  border-radius: 0 0 5px 5px;
}

// Button Modifiers
// --------------------------------------------------
.button-shadow {
  box-shadow: 3px 3px 3px 0 rgba($black, .14);
}

// Button Icon styles
// --------------------------------------------------

.button {
  &.button-icon {
    @include display-inline-flex;
    @include flex-direction(row);
    @include flex-wrap(nowrap);
    @include align-items(center);
    @include justify-content(center);
    vertical-align: middle;

    .icon {
      position: relative;
      top: 1px;
      display: inline-block;
      width: auto;
      height: auto;
      line-height: 0;
      vertical-align: middle;
      transition: 0s;
    }

    &-left {
      .icon {
        margin-right: 15px;
      }
    }

    &-right {
      @include flex-direction(row-reverse);
      .icon {
        margin-left: 15px;
      }
    }
  }
}

.button-icon-only {
  @include reset-button;
  font-size: 0;
  line-height: 0;
  transition: .33s all ease;

  &.button-icon-only-primary {
    @include link($primary, $black);
  }
}

.button-icon-only {
  padding: 9px 18px;
}

.btn-sm {
  font-size: 16px;
}