/*
* @subsection   Google Map
*/
$map-height: 200px;
$map-xs-height: 250px;
$map-sm-height: 400px;
$map-lg-height: 450px;
$map-xl-height: 532px;

// Google Map
.google-map-markers{
  display: none;
}

.google-map-container {
  width: 100%;
}

.google-map {
  height: 250px;
  width: 100%;
  
  @include media-breakpoint-up(xs) {
    height: 450px;
  }
  
  @include media-breakpoint-up(xs) {
    height: 532px;
  }
}

.google-map-container {
  .google-map {
    height: 250px;
    width: 100%;

    @include media-breakpoint-up(md) {
      height: 350px;
    }
  }
}
